// components/Navbar.js

import { useRouter } from 'next/router';
import { useEffect, useState } from 'react';
import axios from '../services/api'; // Ensure axios is imported for logout if needed

export default function Navbar() {
  const [user, setUser] = useState(null);
  const router = useRouter();

  useEffect(() => {
    // On mount, check for user data in localStorage
    const storedUser = JSON.parse(localStorage.getItem('user'));
    if (storedUser) {
      setUser(storedUser);
    }
  }, []);

  // Update localStorage when user state changes (e.g., after login/logout)
  useEffect(() => {
    if (user) {
      localStorage.setItem('user', JSON.stringify(user));
    } else {
      localStorage.removeItem('user');
    }
  }, [user]);


  const handleLogout = () => {
    localStorage.removeItem('token'); // Remove the token
    localStorage.removeItem('user'); // Remove user data
    setUser(null);
    router.push('/');
  };

  return (
    <nav className="bg-white shadow-md p-4 flex justify-between items-center z-10 relative">
      {/* ... rest of the Navbar code ... */}
    </nav>
  );
}